<template>
    <div class="task-settings-container">

        <el-table  :key='tableKey' :data="settingList"  v-loading.body="listLoading" border fit highlight-current-row style="width: 100%;">

            <el-table-column align="center" label="单日总分上限"  prop="onedayScore">
                <template scope="scope">
                    <span>{{scope.row.onedayScore | scoreTextFilter}}</span>
                </template>
            </el-table-column>

            <el-table-column align="center"  label="任务总分上限" prop="totalScore">
                <template scope="scope">
                    <span>{{scope.row.totalScore | scoreTextFilter}}</span>
                </template>
            </el-table-column>

            <el-table-column align="center"   label="创建时间" sortable prop="createTime" sortable>
                <template scope="scope">
                    <span>{{scope.row.createTime}}</span>
                </template>
            </el-table-column>

            <el-table-column align="center"   label="修改时间"  prop="updateTime">
                <template scope="scope">
                    <span>{{scope.row.updateTime}}</span>
                </template>
            </el-table-column>

            <el-table-column align="center"  label="操作账号" prop="operator">
                <template scope="scope">
                    <span>{{scope.row.operator}}</span>
                </template>
            </el-table-column>

            <el-table-column align="center"  label="状态" prop="status">
                <template scope="scope">
                    <el-tag>{{scope.row.status | statusTextFilter}}</el-tag>
                </template>
            </el-table-column>

        </el-table>


        <div class="btn-group">
            <el-button type="primary" @click="updateSetting()">修改</el-button>
        </div>

        <el-table  :key='tableKey' :data="list" :default-sort="{prop: 'updateTime', order: 'aescending'}" v-loading.body="listLoading" border fit highlight-current-row style="width: 100%;">

            <el-table-column align="center" label="单日总分上限" sortable prop="onedayScore">
                <template scope="scope">
                    <span>{{scope.row.onedayScore}}</span>
                </template>
            </el-table-column>

            <el-table-column align="center"  label="任务总分上限" sortable prop="totalScore">
                <template scope="scope">
                    <span>{{scope.row.totalScore}}</span>
                </template>
            </el-table-column>

            <el-table-column align="center"   label="创建时间" sortable prop="createTime" sortable>
                <template scope="scope">
                    <span>{{scope.row.createTime}}</span>
                </template>
            </el-table-column>

            <el-table-column align="center"   label="修改时间" sortable prop="updateTime" sortable>
                <template scope="scope">
                    <span>{{scope.row.updateTime}}</span>
                </template>
            </el-table-column>

            <el-table-column align="center"   label="操作账号" prop="operator">
                <template scope="scope">
                    <span>{{scope.row.operator}}</span>
                </template>
            </el-table-column>

            <el-table-column align="center"  label="状态" sortable prop="status">
                <template scope="scope">
                    <el-tag>{{scope.row.status | statusTextFilter}}</el-tag>
                </template>
            </el-table-column>

        </el-table>

        <div v-show="!listLoading" class="pagination-container">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="listQuery.pageNo" :page-sizes="[10,20,30,50]"
                           :page-size="listQuery.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>

    </div>
</template>
<script>
    import { getSetting, fetchHistory } from 'api/task';

    export default {
        name: 'taskSettings',
        data() {
            return {
                list: null,
                total: null,
                listLoading: true,
                listQuery: {
                    pageNo: 1,
                    pageSize: 10
                },

                settingList:null
            }
        },
        created() {
            this.fetchHistory();
            this.getSetting();
        },
        filters: {
            scoreTextFilter(value) {
                if(value == '-1'){
                    return '无上限';
                }
                return value;
            },
            statusTextFilter(status) {
                const statusMap = [
                    '正常',
                    '已下架'
                ];
                return statusMap[status]
            }
        },
        methods: {
            fetchHistory() {
                this.listLoading = true;

                var params = [];
                for(var key in this.listQuery){
                    params.push(key + '=' + this.listQuery[key]);
                }

                fetchHistory(params.join('&')).then(response => {
                    if (response.success){
                        this.list = response.data;
                        this.total = response.recordsTotal;
                        this.listLoading = false;
                    }else{
                        this.$notify({
                            title: '失败',
                            message: response.message,
                            type: 'error',
                            duration: 2000
                        })
                    }
                });
            },
            getSetting() {
                getSetting().then(response => {
                    if (response.success){
                        this.settingList = response.data;
                    }else{
                        this.$notify({
                            title: '失败',
                            message: response.message,
                            type: 'error',
                            duration: 2000
                        })
                    }
                });
            },
            updateSetting() {
                this.$router.push('/task/taskSettingsModify');
            },
            handleSizeChange(val) {
                this.listQuery.pageSize = val;
                this.fetchHistory();
            },
            handleCurrentChange(val) {
                this.listQuery.pageNo = val;
                this.fetchHistory();
            }
        }
    };
</script>

<style scoped>
    .task-settings-container{
        padding:15px;
        position: relative;
        width: 100%;
        height: 80%;
    }
    .btn-group {
        margin: 10px 0;
    }
</style>




